






















import Vue from 'vue'

export default Vue.extend({
    data: () => ({
        dialog: false,
        data: Object
    }),
    props: {
        title: String,
        text: String,
        agreeText: String,
        disagreeText: String,
    },
    created() {
        this.$root.$refs.confirmDialog = this;
    },
    methods: {
        open(data: any) {
            this.dialog = true;
            this.data = data;
        },
        close() {
            this.dialog = false;
            this.data = Object
        },
        action(agree: boolean) {                
            this.$emit("action", {
                agree: agree,
                data: this.data
            });
            this.close();
        },
    },
});
